.promotion
    padding: 50px 0
    .promotion_title 
        text-align: center
        h2
            font-size: 80px
            color: #747f8d
            word-spacing: 10px
            font-weight: 600
            span
                font-weight: 400
        p
            color: #747f8d
            font-size: 22px
            width: 75%
            margin: 0 auto
@media (max-width: 768px)
    .promotion
        .promotion_title
            h2
                font-size: 22px
                word-spacing: 0
            p
                font-size: 16px
                