.about_sec1
    
    .about_sec1-bg
        background: url('../img/page1-2/background-about.png')
        background-size: cover
        background-position: top
        background-repeat: no-repeat
        .about_item-1
            padding-top: 200px
        .about_sec1-content
            position: relative
            .border-triangle
                position: relative
                height: 100%
                width: 100%
                .border-img
                    position: absolute
                    top: 32%
                    left: -50%
                    img
                        width: 100%

            .about_sec1-img
                img
                    width: 100%
            .about_content-sub
                position: absolute
                bottom: 0
                right: -5%
                width: 55%
                h4
                    background: #fff
                    padding: 10px 0
                    text-transform: uppercase
                    font-size: 32px
                    text-align: right
                    color: #8c8d8e
                    font-weight: 400
                    margin-bottom: 0
                    word-spacing: 5px
    .about_sec1_text
        padding: 40px 0 80px
        .about_title
            text-align: center
            .subtitle
                font-size: 30px
                color: #000000
                text-transform: uppercase
                position: relative
                padding-left: 80px
                font-weight: 400
                &::after
                    content: ''
                    width: 80px
                    height: 1px
                    background: #000 
                    position: absolute
                    top: 50%
                    left: -10px
                    transform: translateY(-50%)
            h2
                font-size: 120px
                color: #747f8f
                letter-spacing: 10px
                font-weight: 700
                span
                    font-size: 120px
                    font-weight: 400
            p
                color: #747f8f
                font-size: 22px
                width: 80%
                margin: 0 auto
        button
            display: block
            margin: 40px auto 0
            background: transparent
            border-radius: 0
            border: 1px solid #747f8f
            color: #747f8f
            box-shadow: none
            text-transform: uppercase
            letter-spacing: 3px
            font-weight: 500
            font-size: 22px
            transition: .5s all
            &:hover
                background: #747f8f
                color: #fff
    @media (max-width: 576px)
        .about_sec1-bg
            .about_item-1
                padding-top: 50px
            .about_sec1-content
                .about_content-sub
                    right: 0
                    h4
                        font-size: 16px
    @media (max-width: 992px)
        .about_sec1_text
            .about_title
                .subtitle
                    padding-left: 0
                    font-size: 18px
                    &::after
                        display: none
                        
                        
                h2
                    font-size: 30px
                    span
                        font-size: 30px
                p
                    font-size: 16px
            button
                font-size: 18px
               