.gallery
    padding: 50px 0
    .gallery_left-box
        margin: 0 30px
        background: #e7eaec
        height: 100%
        .gallery_title
            text-align: center
            padding: 40px 0
            .subtitle
                position: relative
                font-size: 16px
                text-transform: uppercase
                padding-left: 20px
                &::before
                    content: ''
                    width: 50px
                    height: 1px
                    background: #000
                    top: 50%
                    left: -5%
                    position: absolute
                    transform: translateX(-50%)
            h2
                font-size: 40px
                color: #74808e
                text-transform: uppercase
                font-weight: 600
                margin-top: 10px
                span
                    font-weight: 400
            p
                font-size: 16px
                color: #74808e
                padding: 0 5px    
        .gallery_img
            margin: 0 -30px
            .img_left-row
                margin-bottom: 30px
                .img_left-col
                    &:first-child
                        padding-right: 7.5px
                    &:last-child
                        padding-left: 7.5px
                    .item-img
                        margin-bottom: 15px
                        img
                            width: 100%
    .gallery_right-box
        .img_right-row
            margin-bottom: 60px
            &:last-child
                margin-bottom: 0
            .item-img
                margin-bottom: 15px
                img
                    width: 100%
            .gallery_right-item
                padding: 10px 0 0 10px
                position: relative
                &::before
                    content: ''
                    width: calc( 100% - 10px )
                    height: calc( 100% - 10px )
                    bottom: 10px
                    right: 10px
                    border: 1px solid #000
                    position: absolute
                .item-img
                    img
                        position: relative
                        z-index: 9
@media (max-width: 576px)
    .gallery
        .gallery_left-box
            .gallery_title
                .subtitle
                    padding-left: 0
                    &::before
                        display: none
                h2
                    font-size: 22px
                    
                    span
                        font-weight: 400
                p
                    font-size: 16px
                        