.banner
    padding-top: 115px
    .banner_slide
        position: relative
        img
            width: 100%
        .banner_title
            position: absolute
            bottom: 20px
            left: 50%
            transform: translateX(-50%)
            h2
                font-size: 40px
                letter-spacing: 15px
                color: #fff
                font-weight: 600
                text-transform: uppercase
                position: relative
                text-align: center
                &::after
                    content: ''
                    width: 70%
                    height: 1px
                    background: #fff
                    position: absolute
                    bottom: 0
                    left: 49%
                    transform: translateX(-50%)
            .subtitle
                text-transform: uppercase
                font-size: 16px
                text-align: center
                color: #fff
                font-weight: 600
                display: block

