.banner_home
    background: #000
    position: relative
   
    .border_bar-left 
        bottom: 0
        left: 0
        position: absolute
        img
            width: 80%
    .border_bar-right 
        bottom: 0
        right: 0
        position: absolute
        text-align: right
        img
            width: 80%
    .banner_home-box
        padding: 85px 0 0
        margin-bottom: -55px
        .banner_home-gallery
            padding-left: 50px
            position: relative
            bottom: 5%
            .banner_col
                padding: 5px
                
                .banner_img-item1
                    position: relative
                    top: -10%
                    left: 20%
                    z-index: 9
                    
                .banner_img-item2
                    position: relative
                    top: -20%
                    
                .banner_img-item3
                    position: relative
                    bottom: -10%
                    
                .banner_img-item5
                    position: relative
                    top: -40%
                    
                .banner_img-item6
                    img
                        width: 75%
        .banner_home-title
            .banner_title-img
                padding-top: 10%
                .banner_title
                    img
                        width: 60%
    .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction
        bottom: 30px
        z-index: 99
        cursor: pointer
    .swiper-pagination-bullet
        width: 12px
        height: 12px
        background: #ab8838
    .swiper-slide
        padding-top: 0
    
    @media (max-width: 992px)
        .border_bar-left
            img
                width: 60%
        .border_bar-right
            img
                width: 50%
    @media (max-width: 768px)
        .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction
            bottom: 10px
        .banner_home-box
            padding: 50px 0 0
        .border_bar-left
            display: none
        .border_bar-right
            display: none
    @media (max-width: 576px)
        .banner_home-box
            padding: 15px 0 0
            .banner_home-title
                .banner_title-img
                    padding-top: 0