footer
    background-color: #000
    padding: 50px 0
    .footer_content
        .footer_logo
            img
                width: 100px
                margin-bottom: 15px
        .footer_item
            .footer_title
                h3
                    color: #ac8939
                    text-transform: uppercase
                    font-size: 18px
                    letter-spacing: 3px
            .footer_info
                ul
                    padding-left: 0
                    li
                        display: flex
                        align-items: start
                        justify-content: center
                        img
                            width: 15px
                            margin-top: 3px
                            margin-right: 5px
                        p
                            color: #ac8939
                            margin-bottom: 0
                            font-size: 14px
                            
                            span
                                color: #fff
                                opacity: .7
            .footer_social-network
                ul
                    padding-left: 0
                    display: flex
                    margin-bottom: 5px
                    justify-content: center
                    li
                        list-style: none
                        padding-right: 10px
                        
                        i
                            color: #1f1f1f
                            width: 35px
                            height: 35px
                            line-height: 35px
                            background-color: #ac8939
                            border-radius: 50%
                            transition: .5s all
                        &:hover
                            i
                                color: #fff
                                background-color: #69440f