.contact 
    padding: 50px 0
    .contact_map
        iframe
            width: 100%
            height: 600px
    .contact-us
        margin-top: 20px
        .contact_us-info
            border: 1px solid #74808e
            padding: 5px 10px
            .sub_contact
                text-align: center
                text-transform: uppercase
                color: #747f8d
                font-weight: 400
                display: block
                font-size: 18px
            h2
                font-size: 40px
                color: #747f8d
                text-align: center
                font-weight: 500
                position: relative
                &::before
                    content: ''
                    width: 80%
                    height: 2px
                    background:#747f8d
                    position: absolute
                    bottom: 0
                    left: 50%
                    transform: translateX(-50%)
            p
                font-size: 18px
                font-weight: 600
                padding-left: 25px
                color: #747f8d
            .list_info
                ul
                    padding-left: 30px
                    li
                        color: #747f8d
                        font-size: 16px
                        font-weight: 500
            .contact_phone
                padding-left: 10px
                img
                    width: 20px
                    margin-right: 5px
                span
                    color: #747f8d
                    font-weight: 600
            .contact_address
                padding-left: 10px
                img
                    width: 20px
                    margin-right: 5px
                span
                    color: #747f8d
                    font-weight: 500
        .contact_us-form
            .contact_input
                display: flex
                margin-bottom: 20px
                input
                    flex: 50%
                    background: #f3f3f3
                    border: 1px solid #747f8d !important
                    padding: 5px
                    &:first-child
                        margin-right: 10px
                    &:last-child
                        margin-left: 10px    
                    &::placeholder
                        color: #b7b8ba
            .contact_area
                textarea
                    background: #f3f3f3
                    height: 170px
                    border: 1px solid #747f8d!important
                    &::placeholder
                        color: #b7b8ba
            .button_main
                background: #f3f3f3
                color: #747f8d
                margin-top: 20px
                border-radius: 0
                box-shadow: none
                border: 1px solid #747f8d
                display: block
                width: 100%
                transition: .5s all
                &:hover
                    background: #747f8d
                    color: #fff
    @media (max-width: 768px)
        .contact_map
            iframe
                height: 300px
        .contact_us-form
            margin-top: 20px