$btnColor: #3e68ff
.button_main 
	font-size: r(16px)
	border: none
	background-color: transparent
	font-family: inherit
	padding: 0
	cursor: pointer
	// Display
	display: inline-flex
	align-items: center
	justify-content: center
	align-self: start // Optional - see "Gotchas"

	// Visual
	background-color: #ab8938
	color: #fff
	border-radius: 8px
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18)

	// Size
	padding: 0.25em 0.75em
	min-width: 10ch
	min-height: 44px

	// Text
	text-align: center
	line-height: 1.1

	transition: 220ms all ease-in-out

	&:hover,
	&:active
		background-color: scale-color(#69440f, $lightness: -20%)

	&:focus
		outline: none
	
	@media screen and (-ms-high-contrast: active)
		border: 2px solid currentcolor
