.home_mnai-sec1
    // background: url('../img/page1-2/banner-1.png')
    // background-position: top
    // background-size: contain
    // background-repeat: no-repeat
    position: relative
    .sec1_content
        position: absolute
        top: 21%
        width: 58%
        left: 50%
        transform: translateX(-50%)        
        .sec1_content-img
            position: relative
            img
                width: 100%
                position: relative
                z-index: 3
            &::after
                content: ''
                width: 100%
                height: 70%
                top: -15px
                right: -27px
                position: absolute
                background-color: #ab8838
                z-index: 2
    .sec1_subtile
        z-index: 9
        position: absolute
        left: 0
        top: 50%
        transform: translateY(-50%)
        p
            background-color: #fff
            padding: 3px 120px 3px 50px
            color: #8c8d8e
            display: inline-block
            text-transform: uppercase
            font-size: 14px
            letter-spacing: 2px
.home_mnail-sec2 
    background-image: url('../img/page1-2/banner-2.png')
    background-position: top
    background-size: cover
    background-repeat: no-repeat
    .sec2_content 
        padding-top: 33%
        width: 60%
        margin: 0 auto
        .sec2_title
            position: relative
            &::before
                
                content: ''
                top: 13%
                left: -26%
                width: 100%
                height: 100%
                position: absolute
                background: url('../img/page1-2/trang1-2-03.png')
                background-repeat: no-repeat
            h2
                color: #fff
                font-size: 95px
                text-transform: uppercase
                letter-spacing: 3px
                font-weight: 700
                margin-bottom: 0
                
            .subtitle
                color: #fff
                font-size: 50px
                text-transform: uppercase
                letter-spacing: 40px
                line-height: .5
                font-weight: 300
            p
                color: #fff
                font-size: 16px
                text-align: justify
                opacity: .7
            button
                margin-bottom: 20px
                text-transform: uppercase
                font-size: 16px
                letter-spacing: 3px
                background-color: transparent
                border-radius: 0
                border: 1px solid #fff
                padding: 3px 20px
                transform: translateX(200px)
                transition: .5s all
                &:hover
                    background-color: #000
                    // color: #fff

.home_mnail-sec3
    padding: 20px 0
    
    .sec3_content-left
        padding: 0
        .content_left-img
            position: relative
            img
                width: 100%
                position: relative
                z-index: 7
            &::after
                content: ''
                width: 200px
                height: 200px
                position: absolute
                bottom: -7%
                left: -7%
                background: #737f8e
                z-index: 6
        .content_left-text
            border: 1px solid #959da9
            padding: 20px 50px 10px 20px
            margin: 10px -40px 0 15px
            z-index: 10
            position: relative
            h3
                color: #ab8938
                font-size: 28px
                text-transform: uppercase
                font-weight: 400
                letter-spacing: 2px
                text-align: center
                margin-bottom: 0
            span
                color: #737f8e
                font-size: 40px
                text-align: center
                font-weight: 600
                display: block
                line-height: 1
                word-spacing: 3px
                border-bottom: 2px solid #737f8e
            p
                font-size: 16px
                color: #737f8e
                text-align: justify
                margin-bottom: 0
                padding-top: 10px
    .content_right-img
        height: 100%
        position: relative
        img
            position: absolute
            height: 100%
    .sec3_items
        padding: 50px 0 0
        .items_detail
            text-align: center
            margin-bottom: 20px
            .items_detail-img
                margin-bottom: 10px
                img
                    height: 100%
            h4
                color: #ab8738
                font-weight: 400
            p
                font-size: 16px
                text-align: justify
                padding: 0 10px
                color: #737e8b
.home-mnail-sec4
    background: #c8ccd2
    padding: 80px 0 150px
    position: relative
    .sec4_content 
        display: flex
        .sec4_content-title
            h2
                color: #757f8f
                font-size: 40px
                font-weight: 300
            p
                color: #757f8f
                font-size: 16px
        .sec4_img-background 
            position: absolute
            top: -70px
            left: 60%
            img
                width: 350px
                pointer-events: none
// Section 5
.home-mnail-sec5
    background: #edeff1
    padding: 50px 0
    position: relative
    .sec5_content-right
        .content_right-title
            margin-bottom: 30px
            h3
                color: #757f8f
                font-size: 38px
                font-weight: 300
            p
                color: #757f8f
                font-size: 16px
        .content_right-carousel
            ul
                padding: 0
                li
                    list-style: none
                    display: flex
                    margin-bottom: 20px
                    .content_img-avt
                        width: 100px
                        margin-right: 10px
                        img
                            margin-top: 10px
                            width: 100%
                            display: block
                    .content-detail
                        p 
                            color: #757f8f
                            font-size: 16px
                            text-align: justify
                            line-height: 1.3
                        h3
                            color: #ab8738
                            text-transform: uppercase
                            margin-top: 20px
                            font-size: 15px
                            font-weight: 400
            .slick-dots
                display: flex
                left: 50px
                bottom: 10px
                li
                    margin: 0
                    button
                        &::before
                            font-size: 12px
                            color: #737f8e
    .sec5_content-left
        height: 420px
        .content_left-img
            text-align: right
            position: relative
            top: -65%
            &::after
                content: ''
                width: 70%
                height: 110%
                right: -20px
                top: -20px
                position: absolute
                z-index: 8
                background: linear-gradient(to top left, #b29247, #ddcfb0)
            &::before
                content: ''
                width: 85%
                height: 69%
                bottom: -23px
                right: -50px
                z-index: 9
                position: absolute
                border-right: 1px solid #876637
                border-bottom: 1px solid #876637
            img
                width: 95%
                position: relative
                z-index: 9       
    .img-overlay
        position: absolute
        top: 70%
        left: 50%
        transform: translate(-50%, -50%)
        pointer-events: none
        img
            width: 300px 
    .dots-overlay
        display: flex
        flex-direction: column
        position: absolute
        top: 30%
        left: 51%
        color: #747f8e
        transform: translate(-50%, -50%)
        i
            margin-bottom: 15px
// Section 6 - blog
.home_mnail-sec6
    .mnail_content_start
        .mnail_sec6-img
            &:first-child
                margin-bottom: 30px
            img
                width: 100%
    .mnai_content-center
        display: flex
        flex-direction: column
        .mnail_sec6-img
            margin-bottom: 20px
            &:last-child
                margin-bottom: 0
            .center_img1
                flex: 1
            .center_img2
                flex: 9
            .center_img3
                flex: 2
        img
            width: 100%
    .mnail_sec6-content
        .sec6_content-end
            padding: 50px 0
        .content_end-title
            h3
                font-size: 50px
                color: #747f8e
                text-transform: uppercase
                letter-spacing: 3px
                font-weight: 700
                text-align: center
                font-style: italic
                span
                    text-align: right
                    display: block
            .subtitle
                font-size: 26px
                text-align: right
                display: block
                letter-spacing: 3px
                color: #ab8738
                font-weight: 300
                line-height: 1
                position: relative
                padding-bottom: 20px
                &::before
                    content: ''
                    width: 80%
                    height: 1px
                    background: #747f8e
                    right: 0
                    bottom: 0px
                    position: absolute
        .content_end-text
            margin-top: 20px
            h4
                color: #747f8e
                font-size: 28px
                letter-spacing: 5px
                font-weight: 600
                margin-bottom: 0
            span
                color: #ab8738
                font-size: 22px
                letter-spacing: 3px
                line-height: 1
                font-weight: 300
            p 
                color: #747f8e
                font-size: 16px
                margin-top: 5px
        .arrow-right
            padding-top: 50px
            a
                text-decoration: none
                transition: .5s all
                i
                    color: #ab8738
                    padding-right: 10px
                span
                    color: #747f8e
                    font-size: 15px
                &:hover
                    span
                        color: #ab8738
// Responsive
@media (max-width: 992px)
    .home_mnai-sec1
        .sec1_subtile
            display: none
    .home_mnail-sec2 
        .sec2_content
            .sec2_title
                &::before
                    display: none
                h2
                    font-size: 50px
                    line-height: 1.5
                .subtitle
                    font-size: 30px
                p
                    font-size: 15px
                    margin-top: 10px
                button
                    transform: translate(25%)
    .home_mnail-sec3
        .sec3_content-left
            .content_left-text
                margin: 15px 0
    .home-mnail-sec4
        padding: 50px 0
    .home-mnail-sec5
        .sec5_content-left
            height: auto
            .content_left-img
                top: 0
        .dots-overlay
            display: none
        