header
    position: fixed
    width: 100%
    margin: 0 auto
    z-index: 999
    box-shadow: 1px 0 15px rgba(0, 0, 0, 0.5)
.header_top
    background: #1f1f1f
    padding: 10px 0
    .container-fluid
        padding: 0 100px
.header_top-item
    display: flex
    align-items: center
    justify-content: space-between
    .items_social-network
        ul
            padding: 0
            display: flex
            align-items: center
            margin-bottom: 0
            li
                list-style: none
                margin-right: 10px
                
                font-size: 14px
                a
                    color: #ac8939
                    transition: .5s all ease-in-out
                    &:hover
                        color: #69440f
                
    .items-contact
        ul
            display: flex
            align-items: center
            padding: 0
            margin-bottom: 0
            li
                list-style: none
                margin-right: 30px
                a
                    color: #ac8939
                    display: flex
                    align-items: center
                    font-size: 14px
                img
                    width: 15px
                    margin-right: 5px 
@media (max-width: 768px)
    .header_top-item
        justify-content: center
        .items-contact
            display: none
@media (max-width: 992px)
    .header_top-item
        .items-contact
            ul
                li
                    margin-right: 10px
                    &:last-child
                        margin-right: 0
// Header_bottom
.header_bottom
    background: #000
    .container-fluid
        padding: 0 50px
    .navbar-brand
        padding: 0
        img
            width: 50px
            height: 60px
    .navbar-toggler
        border-radius: 3px
        background-color: #1f1f1f
        i
            color: #ac8939 !important
        &:focus
            outline: 1px solid #ac8939
            
    .navbar-nav
        display: flex
        justify-content: space-between
        width: 100%
        padding: 0 120px 0 50px
        li
            text-align: center
            position: relative
            transition: .5s all ease-in-out
            .nav-link
                text-transform: uppercase
                color: #ac8939
                opacity: .5
                font-weight: 500
                padding: 0
                text-decoration: none
            a 
                text-transform: uppercase
                color: #ac8939 !important
                opacity: 1
                font-weight: 500
                padding: 0
                text-decoration: none
               

            &::after
                content: ''
                width: 0
                height: 1px
                background: #ac8939
                bottom: 0
                display: block
                margin: 0 auto
                transition: .5s all
            &:hover
                &::after
                    width: 100%
            &.active, &:hover
                .nav-link
                    opacity: 1
                    color: #ac8939
            .dropdown-menu
                margin: 20px 0 0
                background-color: #1f1f1f
                padding: 10px 10px 10px 20px
                width: 200px
                a
                    color: #ac8939
                    opacity: .5
                    margin-bottom: 10px
                    &:hover
                        opacity: 1
                        background-color: transparent
                        
            
    .nav_button
        display: flex
        align-items: center
        justify-content: center
        .button_gallery
            margin-right: 10px
            img
                width: 35px
                transition: .5s all
                &:hover
                    transform: rotate(90deg)
        .button_book
            .button_main
                min-width: 50px !important
                padding: 0 5px
                text-transform: uppercase
                font-size: 13px
    @media (max-width: 992px)
        .navbar-nav
            padding: 0 0 20px 0
            
            li
                &::after
                    display: none
            .nav-link
                margin-bottom: 10px
                
        .nav_button
            // display: none
            padding-bottom: 20px
                
            