.login
    background: url('../img/login/background.png')
   
    background-size: cover
    background-position: center
    background-repeat: no-repeat
    padding-top: 110px
    position: relative
    height: 100vh
    .login_box
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        .login_title
            h2
                color: #000
                font-size: 40px
                font-weight: 600
                margin-bottom: 20px
        .login_form
            background: rgba(0, 0, 0, 0.5)
            padding: 15px
            position: relative
            &::after
                content: ''
                width: 0
                height: 0
                bottom: -20px
                left: 50%
                position: absolute
                transform: translateX(-50%)
                border-left: 20px solid transparent
                border-right: 20px solid transparent
                border-top: 20px solid rgba(0, 0, 0, 0.5)
            .login_icon
                text-align: center
                padding: 15px 0
                i
                    font-size: 35px
                    color: #b1c8c8
                    width: 70px
                    height: 70px
                    line-height: 70px
                    border: 2px solid #b1c8c8
                    border-radius: 50%
            .login_input
                .input-group
                    input
                        background: #cbcbcb
                        &:focus
                            outline: none
                            box-shadow: none
                            border: none
                .input_feature
                    display: flex
                    align-items: center
                    justify-content: space-between
                    .input_check
                        display: flex
                        align-items: center
                        label
                            color: #fff
                            font-size: 12px
                            margin-bottom: 0
                            padding-left: 5px
                            opacity: .5
                    a
                        
                        font-size: 12px
                        color: #fff
                        opacity: .5
                .button_login
                    display: inline-block
                    width: 100%
                    margin: 20px 0 10px
                    border-radius: 0
                    box-shadow: none
                    text-transform: uppercase
                    font-size: 14px
                    font-weight: 500
                    background: #000
                    transition: .5s all
                    &:hover
                        background: #ac8939