.svc-waxing
    .waxing_content 
        .waxing_content-bg
            position: relative
            width: 100%
            height: 100%
            &::before
                content: ''
                width: 100%
                height: 100%
                top: 0
                left: 0
                position: absolute
                background: #747f82
        .waxing_col-center
            padding-left: 0
            padding-right: 5px
            .waxing_content-img
                padding: 10px 10px 0 0
                &::before
                    content: ''
                    width: calc( 100% - 5px )
                    height: calc( 100% - 20px )
                    left: 0
                    top: 0
                    position: absolute
                    background: #747f82
                    
                img
                    width: 100%
                    position: relative
                    z-index: 9
        .waxing_text-box
            width: 100%
            height: 100%
            display: flex
            align-items: flex-end
            
            .waxing_content-text
                background: #fff
                border-radius: 10px
                padding: 20px 10px
                width: 98%
                height: 90%
                position: relative
                z-index: 10
                
                h3 
                    color: #747f82
                    font-size: 18px
                    text-align: center
                    margin-top: 20px
                    margin-bottom: 10px
                .waxing_content-list
                    
                    ul
                        padding-left: 10px
                        li
                            list-style: none
                            i
                                font-size: 10px
                                color: #747f82
                            span
                                font-size: 16px
                                color: #747f82
                                margin-left: 10px
            .waxing_tex-border
                position: absolute
                bottom: 20px
                right: 26px
                z-index: 8
                border: 1px solid #747f82
                border-radius: 10px
                width: 95%
                height: 90%
@media (max-width: 576px )
   .svc-waxing
        .waxing_content
            .waxing_text-box
                height: auto
                margin-top: 20px
    