.svc_massages
    .massages_content-box
        background: #e5e6e2
        .massage_img
            padding: 30px
            position: relative
            &::before
                content: ''
                width: 100%
                height: 65%
                top: 0
                left: 0
                background: #9f7f38
                position: absolute
            img
                width: 100%
                position: relative
        .massages_text
            display: flex
            justify-content: center
            align-items: center
            padding: 0 20px
            h2 
                font-size: 40px
                color: #747f82
                width: 50%
                text-transform: uppercase
                padding: 0 0 20px 40px
                font-weight: 400
                
            .massages_list-left
                padding-left: 80px
                width: 50%
                position: relative
                &::after
                   content: ''
                   width: 100%
                   height: 1px
                   right: -15px
                   bottom: -10px
                   background: #cbcbc8
                   position: absolute 
                li
                    color: #9f7f38
                    span
                        font-size: 18px
        .massages_info
            background: #747f82
            padding: 5px 20px
            p
                margin-bottom: 0
                color: #fff
                font-size: 12px
                text-transform: uppercase
                letter-spacing: 5px
                padding-left: 20%
                opacity: .7
    .massages_box_right
        height: 100%
        display: flex
        flex-direction: column
        justify-content: flex-end
        position: relative
        &::before
            content: ''
            width: 100%
            height: 20px
            background: #747f82
            top: 30px
            left: 0
            position: absolute
        .massages_item-img
            display: flex
            .item_img-1
                flex: 50%
                position: relative
                &::before
                    content: ''
                    width: 25%
                    height: calc( 100% + 40px )
                    top: -20px
                    left: 0
                    background: #747f82
                    position: absolute
                img
                    width: 100%
                    position: relative
                    z-index: 10
            .item_img-2
                flex: 50%
                position: relative
                &::before
                    content: ''
                    width: calc( 100% + 30px  )
                    height: calc( 100% + 40px )
                    top: -20px
                    right: 0
                    background: #9f7f38
                    position: absolute
                img
                    width: 100%
                    position: relative
        .massages_text
            h3
                font-size: 30px
                color: #747f82
                text-transform: uppercase
                padding: 20px 0 20px 40px
                font-weight: 400
                width: 50%
            .massages_list-right
                padding-left: 80px
                width: 50%
                position: relative
                &::after
                   content: ''
                   width: 180%
                   height: 1px
                   right: -15px
                   bottom: -16px
                   background: #cbcbc8
                   position: absolute 
                li
                    color: #9f7f38
                    span
                        font-size: 18px
    .svc_massages-contact
        text-align: center
        margin-top: 40px
        margin-bottom: 20px
        h4
            font-size: 20px
            color: #737f82
            font-weight: 400
            word-spacing: 3px
    @media (max-width: 992px)
        .massages_content-box
            .massages_text
                h2
                    font-size: 30px
                    padding-left: 0
                .massages_list-left
                    padding-left: 40px
                    li
                        span
                            font-size: 14px
        .massages_box_right
            .massages_text
                h3
                    padding-left: 0
                    font-size: 22px
                .massages_list-right
                    padding-left: 20px
                    li
                        span
                            font-size: 14px
    @media (max-width: 768px)
        .massages_content-box
            margin-bottom: 30px
        .massages_box_right
            .massages_text
                h3
                    padding-left: 20px
                .massages_list-right
                    padding-left: 80px
                 
                
            
    @media (max-width: 576px)
        .massages_content-box
            .massages_text
                h2
                    font-size: 20px
                    padding-left: 0
                .massages_list-left
                    padding-left: 20px
                    li
                        span
                            font-size: 14px
        .massages_box_right
            .massages_text
                margin-top: 20px
                h3
                    padding-left: 0
                    font-size: 16px
                .massages_list-right
                    padding-left: 20px