$scrollLength: 100vh

@media(prefers-reduced-motion: no-preference)
	html
		scroll-behavior: smooth
main 
	position: relative

.back-to-top-wrapper
	position: absolute
	top: $scrollLength
	right: 0.25rem
	bottom: -5em
	width: 3em
	pointer-events: none

.back-to-top-link
	position: fixed
	position: sticky
	pointer-events: all
	top: calc(100vh - 5rem)
	display: inline-block
	text-decoration: none
	font-size: 2rem
	line-height: 2rem
	text-align: center
	width: 3rem
	height: 3rem
	padding: 0.25rem
	border: 1px solid #aa893a
	transition: transform 80ms ease-in
	z-index: 998
	&:hover,
	&:focus 
		transform: scale(1.1)
	&:focus 
		outline: none
		// box-shadow: 0 0 0 3px scale-color($main, $lightness: 35%)
	i
		font-size: 20px
		color: #aa893a
		
