.clear
    clear: both
.svc-nails-sec1
    padding: 80px 0 30px
    background: #eaebec
    .nails_sec1-introduction
        padding: 0 10% 50px 10%
        text-align: center
        h2
            color: #9f7f38
            font-weight: 300 
            letter-spacing: 3px
            font-size: 30px
    .nails_sec1-content
        .content_item
            &:first-child
                margin-bottom: 80px
    .sec1_content-img
        margin-bottom: 20px
        img
            width: 100%
            
    
    .sec1_content-describe
        padding-left: 15px
        h4 
            line-height: 1
            color: #737f82
            font-size: 18px
            font-weight: 600
        span
            color: #737f82
            font-size: 18px
        p
            color: #737f82
            font-size: 18px
            text-align: justify
            padding-bottom: 20px
            padding-right: 10%
.svc-nails-sec2 
    background: #eaebec
    padding: 0 0 20px
    .nails_sec2-combo
        .sec2_combo-describe
            h4
                color: #747f82
                font-size: 36px
                font-weight: 400
                margin-top: 30px
                
            p
                color: #747f82
                font-size: 18px
                margin-bottom: 5px
        .sec2_combo-img
            img
                width: 100%
                border-radius: 20px
    .nails_sec2-services
        .sec2_services-box
            width: 85%
            margin: 0 auto
            background: #fff
            padding-bottom: 80px
        .sec2_service-title
            text-align: center
            margin-top: 40px
            h4
                color: #737e81
                font-size: 26px
                font-weight: 400
                padding-top: 20px
                margin-bottom: 40px
        .sec2_service-items
            width: 80%
            margin: 0 auto
            .col-6.col-lg-3
                padding: 0
            .service_item
                text-align: center
                span
                    font-size: 80px
                    color: #fff
                    margin-top: 20px
                    font-weight: 300
                    opacity: .7
                h3
                    color: #fff
                   
                    font-weight: 400
                    font-size: 20px
                p
                    color: #fff
                    font-size: 18px
                    opacity: .7
                    margin-bottom: 0
                    
            .flex_item-1
                background: #747f82
                padding: 20px 10px
                height: 320px
                margin: 80px 0 0 0
            .flex_item-2
                background: #ced3d4
                padding: 30px 15px
                img 
                    border-radius: 9px
    .nails_sec2-kidmenu
        .sec2_kidmenu-status
            padding: 30px 0 30px
            h4
                font-size: 18px
                color: #9f8037
                text-align: center
                padding: 0 10%
                font-weight: 400
        .sec2_kidmenu_item
            width: 55%
            margin: 0 auto
            .kidmenu_item-left
                background: #cdd1d2
                padding: 15px
                img
                    width: 100%
            .kidmenu_item-right
                background: #748082
                .kidmenu_right-text
                    height: 100%
                    color: #fff
                    display: flex
                    flex-direction: column
                    justify-content: center
                    padding: 10px
                    
                    h3
                        font-size: 18px
                        // padding-top: 35px
                    p
                        font-size: 16px
                        opacity: .7
                        margin-bottom: 5px
.svc-nails-sec3
    background: #eaebec
    padding: 20px 0 40px
    .blog_list 
        display: flex
        ul
            padding-left: 0
            float: left
            width: 55%
            margin-bottom: 0
            li
                list-style: none
                margin-bottom: 10px
                i
                    color: #737f82
                    font-size: 10px
                span
                    color: #737f82
                    font-size: 18px
                    margin-left: 10px
            img
                padding-right: 10px

        .blog_list-img
            float: left
            width: 45%
            img
                width: 100%
                height: 100%
    .nails_sec3-additional
        width: 90%
        margin: 0 auto
        background: #fff
        padding: 50px 0 30px 60px
        border-radius: 8px
        .sec3_additional-list
            h3
                font-size: 18px
                color: #737e81
                padding-left: 20px
            ul
                padding-left: 0
                li
                    list-style: none
                    margin-bottom: 15px
                    i
                        font-size: 10px
                        color: #737f82
                    span
                        margin-left: 10px
                        font-size: 16px
                        color: #737f82
        .sec3_additional-right
            padding-left: 0
        .sec3_additional-box
            position: relative
            width: 100%
            height: 100%
            padding-left: -20px
            &::before
                content: ''
                width: calc( 100% + 10px )
                height: 100%
                top: 0
                left: -10px
                position: absolute
                background: #737f82
            .additional_img
                padding: 80px 20px 40px 20px
                position: absolute
                top: -50px
                left: 50px
                width: 70%
                background: #9f7f38
                img
                    width: 100%
                    border-radius: 8px
    .nails_sec3-acrylic 
        padding: 40px 0
        width: 90%
        margin: 0 auto
        .sec3_acrylic-text
            h4
                color: #737f82
                font-size: 20px
            p
                color: #737f82
                font-size: 18px
                line-height: 1.8
                opacity: .8
                margin-bottom: 10px
                text-align: justify
                padding-right: 15px
            ul
                padding-left: 0
                li
                    list-style: none
                    i
                        color: #737f82
                        font-size: 10px
                    span
                        color: #737f82
                        font-size: 16px
                        margin-left: 10px
        .sec3_acrylic-right
            padding-left: 0
            .sec3_acrylic-img
                width: calc(100% + 20px)
                margin-left: -20px
                img
                    width: 100%
    .nails_sec3-blogs
        @media (min-width: 992px)
            .col-lg-6
                &:first-child
                    padding-right: 5px
                &:last-child
                    padding-left: 5px
        
        .sec3_blog-content
            background: #fff
            border-radius: 8px
            padding: 30px 5px 15px 20px    
            h4
                color: #737f82
                font-size: 20px
            p 
                color: #737f82
                font-size: 18px
                line-height: 1.8
                padding-right: 30px
                text-align: justify   
                opacity: .8 
            
        .sec3_blog-left
            height: 100%
            .blog_list
                ul
                    margin-top: 20px
        .sec3_blog-right
            padding-left: 100px
    .nails_sec3-blogend
        padding: 40px 0
        @media (min-width: 992px)
            .sec3_blogend-left 
                .sec3-bloged-img
                    margin-left: 20px
                    margin-right: -20px
        .img_border
            margin-bottom: 20px
            &::after
                border-color: #aeb5b7
        .sec3_blogend-right
            padding-left: 30px
            .sec3-bloged-text 
                h4
                    color: #748082
                    font-size: 20px
                    line-height: 0
                    margin-bottom: 20px
                   
                p
                    color: #737f82
                    font-size: 18px
                    line-height: 1.8
                    opacity: .8
                    margin-bottom: 10px
                    text-align: justify
                    padding-right: 15px
                

// Responsive screen 992px
@media (max-width: 992px)
    .nails_sec2-services
        .sec2_services-box
            .sec2_service-items
                
                .flex_item-1
                    margin: 0 !important
                    height: 100%
    .svc-nails-sec3
        .nails_sec3-additional
            .sec3_additional-box
                .additional_img
                    top: 50px
    .svc-nails-sec3
        .sec3_acrylic-right
            .sec3_acrylic-img
                width: 100% !important
                margin-left: 0 !important
    .svc-nails-sec1
        .nails_sec1-introduction
            h2
                font-size: 18px
@media (max-width: 768px)
    .svc-nails-sec3
        .nails_sec3-additional
            .sec3_additional-box
                .additional_img
                    display: none
@media (max-width: 576px)
    .nails_sec2-services
        .sec2_services-box
            .sec2_service-items
                .service_item
                    span
                        font-size: 30px
                    h3
                        font-size: 15px
                    p
                        font-size: 14px
    .svc-nails-sec3

        .blog_list
            ul
                width: 100% !important
    
        .blog_list-img
            display: none
    .nails_sec3-blogs
        .sec3_blog-right
            padding-left: 20px !important
        